import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SeccionPlataformaPage } from './seccion-plataforma.page';

const routes: Routes = [
  {
    path: '',
    component: SeccionPlataformaPage,
  },
  {
    path: 'descripcion-actividad',
    loadChildren: () =>
      import('src/app/pages/SST/descripcion-actividad/descripcion-actividad.module').then(m => m.DescripcionActividadPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SeccionPlataformaPageRoutingModule {}
